import { Component, OnInit, Output, Input, OnChanges, Renderer2 } from '@angular/core';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Message } from 'primeng/primeng';
import { Router, ActivatedRoute } from '@angular/router';
import { ProjectHttpService } from 'app/pages/tzb/customCenter/project-main-manage/http/project-main-manage.service';
import { coopProjectElementQuery, coopProjectElementAdjust } from '../bean/project-protocol-query.bean';
import { CALENDAR_CN, tableMessage } from "app/@uisftech/common/constant/config";
import { CustomeHttpService } from 'app/@uisftech/view/http/uisftech-view.http.service';
import { nextBean } from 'app/pages/tzb/customCenter/project-main-manage/private-components/sunject-of-cooperation/bean/sunject-of-cooperation.bean';
import { CommonlyMessage } from "app/pages/tzb/custom/overdue/common/msg";
import { ElementRef, ViewChild, ViewContainerRef, ComponentRef, ComponentFactoryResolver, ComponentFactory } from '@angular/core';
import { AdjustInputComponent } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/templates/adjust-input.component";
import { DomAdjust } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/tool/adjust";
import { MessageService as MessageService2 } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/templates/MessageService";
import { HttpService as HttpService2 } from "app/@uisftech/common/service/http.service";
import { SelectPerson } from "app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/tool/selectPerson";
import { DomFactory } from 'app/pages/tzb/customCenter/project-main-manage/project-main-manage-add/project-protocol-query/subject-adjustment/tool/domFactory';

// 查询轨迹
import { ProcessInstanceManagementService } from 'app/pages/tzb/workflow/activiti-work-flow/process-instance/process-instance-management/process-instance-management.service';
import { HttpService } from 'app/pages/tzb/workflow/activiti-work-flow/common/service/http.service';
import { MessageService } from 'primeng/components/common/messageservice';
import { ObjectHelper } from "app/pages/tzb/custom/overdue/common/objecthelper";
import { CustomCenterHttpService } from "app/pages/tzb/customCenter/http/custom-center.http.service";
import { LocalData } from "app/pages/tzb/custom/overdue/common/localData";

@Component({
    selector: "subject-adjustment",
    templateUrl: './subject-adjustment.component.html',
    styleUrls: ['./subject-adjustment.component.scss'],
    // providers: [MessageService2]
    // 查询轨迹
    providers: [Commfunc, ProcessInstanceManagementService, HttpService, MessageService, MessageService2, CustomCenterHttpService]
})

export class SubjectAdjustment implements OnInit {
    afterXX: any = '';
    adjustInfo: any;
    adjustSeqno: any;
    detilells: any;
    detilell: any;
    postId: any;
    user: any;
    // ------------------------------------------------
    code: any;

    commonlyMessage: CommonlyMessage = new CommonlyMessage();
    selectPerson: SelectPerson = new SelectPerson(this.commfunc, this.httpWork, this.commonlyMessage, this.router);;
    domAdjust: DomAdjust;

    projectId: string = '';
    userId: string = '';
    custId: string = '';
    adjustElement: string = '';   // 调整类型
    adjustReason: string = '';   // 调整原因
    adjustOpinion: string = '';   // 调整原因
    taskId: string = '';
    // 路由带录值
    lei: string;

    msgss: any = [];

    domFactory: DomFactory;

    isRecover: boolean = false;


    theRequest: any = [];
    isConmit: boolean = false;

    objectHelper: ObjectHelper = new ObjectHelper();
    adjustElementList: any;

    @ViewChild("beforeContainer", { read: ViewContainerRef }) beforeContainer: ViewContainerRef;
    @ViewChild("afterContainer", { read: ViewContainerRef }) afterContainer: ViewContainerRef;

    localData: LocalData = new LocalData(this.commfunc);
    mainFlowNo: string = '';

    constructor(
        public httpService: HttpService2,
        public router: Router,
        public activatedRoute: ActivatedRoute,
        public http: ProjectHttpService,
        public commfunc: Commfunc,
        public httpWork: CustomeHttpService,
        private resolver: ComponentFactoryResolver,
        public msgService: MessageService2,
        public customService: CustomeHttpService,
        public customCenterHttpService: CustomCenterHttpService,
        // 查询轨迹
        //public httpService1: CustomeHttpService,
        //private processInstanceManagementService: ProcessInstanceManagementService,
    ) { }

    ngOnInit() {
        this.codeValues();
        this.initFunc();
    }

    // 初始化函数
    initFunc() {
        
        // this.userId = JSON.parse(this.commfunc.getSessionData('officeOrgId'));
        this.userId = JSON.parse(this.commfunc.getSessionData('commonHeader')).userId;

        this.activatedRoute.queryParams.subscribe(queryParams => {
            if (this.activatedRoute.snapshot.params.mainFlowNo) {
                this.mainFlowNo = this.activatedRoute.snapshot.params.mainFlowNo;
            }
            // 审批流程
            if (this.activatedRoute.snapshot.params.from == 'reminder-do') {
                let t = this.commfunc.getSessionDataCH('change-detail-from-reminder-do');
                t = JSON.parse(t);
                this.detilell = t['dataMap'];
                this.detilells = JSON.parse(this.detilell)
                this.projectId = this.detilells['bizPkFieldMap']['projectId']
                this.adjustElement = this.detilells['bizPkFieldMap']['adjustElement']
                this.adjustSeqno = this.detilells['bizPkFieldMap']['adjustSeqno']
                this.adjustReason = this.detilells['bizPkFieldMap']['adjustReason']
                this.adjustOpinion = this.detilells['bizPkFieldMap']['adjustOpinion']
                this.adjustInfo = this.detilells['bizPkFieldMap']['adjustInfo']
                this.taskId = this.activatedRoute['snapshot']['params']['taskId']
                this.isRecover = true;
                this.queryAdjustElement(() => {
                    this.loadConfig1()
                })
            } else {
                // 第一次要素调整
                this.projectId = queryParams['projectId'];
                this.custId = queryParams['custId'];
                // this.taskId = queryParams['taskId'];
                this.lei = queryParams['lei'];
                this.queryAdjustElement(() => {
                    this.loadConfig();
                })
            }
        })
    }

    queryAdjustElement(callback: Function) {
        let param = { "projectId": this.projectId };
        this.http.queryAdjustElement(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                let list = data['elementMap']['adjustList'];
                let i = 'adjustValue';
                this.ZHEn(list, i);
                // this.adjustElementList = this.fixAdjustElement(data['elementMap']['adjustList']);
                if (callback) callback();
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        })
    }
    // 要素中文排序
    ZHEn(a, i) {
        function getval(option) {
            if (!i) return option;
            let k = option;
            i.split('.').filter(function (item) {
                k = k[item]
            })
            return k + ''
        }
        a = a.sort(function (it1, it2) {
            return getval(it1).localeCompare(getval(it2), 'zh-CN')
        })
        this.adjustElementList = this.fixAdjustElement(a);
    }
    fixAdjustElement(list) {
        let ret = [];

        if (list && list.length) {
            ret.push({ label: '请选择', value: '' });
            list.forEach(element => {
                let obj = {
                    label: element.adjustValue,
                    value: element.adjustKey
                }
                ret.push(obj);
            });
        }

        return ret;
    }

    loadConfig1() {
        let param = { "adjustSeqno": this.adjustSeqno };
        this.http.coopEleAdjustHistoryDetailQuery(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                let list = data.coopEleAdjustHistoryMap.adjustInfo;
                this.productFixer.setOrigin(list);
                if (this.adjustElement == '013' && this.productFixer.isNeedFixProduct(list)) {
                    list = this.productFixer.fixProduct(list);
                }

                // this.bean = data.coopEleAdjustHistoryMap;
                this.domAdjust = new DomAdjust(this.httpService, this.resolver, this.msgService);
               
                this.domAdjust.configCover(list, { custId: '', commonlyMessage: null, isRecover: this.isRecover, adjustElement: this.adjustElement,projectId:this.projectId });
                this.specialDataOperateForSupply();

                this.domFactory = new DomFactory();
                this.domFactory.loadBasicData({
                    beforeContainer: this.beforeContainer,
                    afterContainer: this.afterContainer,
                    domAdjust: this.domAdjust,
                    isDisabled: false
                })
               

                const before = this.beforeDecorate(this.domAdjust.before);//by huj++
                const after = this.afterDecorate(this.domAdjust.after);//by huj++
 
                this.domFactory.generatorDom(before, 1);//by huj++
                this.domFactory.generatorDom(after, 2);//by huj++

                this.domAdjust.recoverDataMap();
                this.adjustReason = data.coopEleAdjustHistoryMap.adjustReason
                this.adjustOpinion = data.coopEleAdjustHistoryMap.adjustOpinion
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        })
    }
    loadConfig() {
        let param = { "projectId": this.projectId, "adjustElement": this.adjustElement };
        this.http.coopProjectElementQuery(param).subscribe(data => {
            if (data['returnCode']['code'] == 'success') {
                let list = data.beforeAdjustList;
                this.productFixer.setOrigin(list);
                if (this.adjustElement == '013' && this.productFixer.isNeedFixProduct(list)) {
                    list = this.productFixer.fixProduct(list);
                }
                this.domAdjust = new DomAdjust(this.httpService, this.resolver, this.msgService);

                
                this.domAdjust.configCover(list, { custId: this.custId, commonlyMessage: this.commonlyMessage, adjustElement: this.adjustElement,projectId:this.projectId });
                this.specialDataOperateForSupply();

               const before = this.beforeDecorate(this.domAdjust.before);//by huj++
               const after = this.afterDecorate(this.domAdjust.after);//by huj++

               this.generatorDom(before, 1);//by huj++
               this.generatorDom(after, 2);//by huj++

            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        })

    }

    beforeDecorate (before: any[]) {
        before.forEach((element, index) => {
            if (element.elementType === '3'||element.elementType === '1'
                ||element.elementType === '2') {
                element.id = this.adjustElement + '_' + element.elementEnName + '_before_' + index;
            }
        })
        return before;
    }

    afterDecorate (after: any[]) {
        after.forEach((element, index) => {
            if (element.elementType === '3'||element.elementType === '1'
                ||element.elementType === '2') {
                element.id = this.adjustElement + '_' + element.elementEnName + '_after_' + index;
            }
           
        })
        
       return after;
    }

    // 获取供应链模式变更初始值
    getModeChangeInitData() {
        let ret = {
            middleAccountNumber: 0
        }
        if (this.adjustElement == '014') {
            let temp = this.domAdjust.before.filter(x => x.elementNumber == '1');
            if (temp && temp.length) {
                ret.middleAccountNumber = temp[0].elementValue;
            }
            let temp1 = this.domAdjust.after.filter(x => x.elementNumber == '1');
            if (temp1 && temp1.length) {
                temp1[0].elementValue = temp[0].elementValue;
            }
        }

        return ret;
    }


    productFixer = {
        parent: this,
        origin: [],
        changedProduct: [],
        setOrigin(list) {
            this.origin = this.parent.objectHelper.objectDeepCopy(list);
        },
        isNeedFixProduct(list) {
            let flag = false;
            let t = list.filter(x => x.elementEnName == 'productId');
            if (t && t.length > 1) {
                flag = true;
            }
            return flag;
        },
        getCombinedValue(list, key) {
            let ret = '';
            if (list && list.length) {
                list.forEach(element => {
                    ret += element[key] + ',';
                });
                ret = ret.substr(0, ret.length - 1);
            }
            return ret;
        },
        getCombinedList(list, key, displayKey) {
            let allItems = this.getCombinedValue(list, key);
            let item = this.parent.objectHelper.objectDeepCopy(list[0]);
            item[key] = allItems;
            item.displayFlag1 = displayKey;
            return item;
        },
        columns() {
            return this.parent.isRecover ? ['elementValueBefore', 'elementValueAfter', 'elementDescBefore', 'elementDescAfter'] : ['elementValue', 'elementValue', 'elementDesc', 'elementDesc']
        },
        fixProduct(list) {
            let ret = [];
            let columns = this.columns();

            if (list && list.length > 1) {
                // 调整前
                let ids = list.filter(x => x.elementEnName == 'productId' && x.displayFlag1 != '1');
                let t = this.getCombinedList(ids, columns[0], '0');
                ret.push(t);

                let names = list.filter(x => x.elementEnName == 'productName' && x.displayFlag1 != '1');
                t = this.getCombinedList(names, columns[0], '0');
                ret.push(t);

                // 调整后
                ids = list.filter(x => x.elementEnName == 'productId' && x.displayFlag1 != '0');
                t = this.getCombinedList(ids, columns[1], '1');
                ret.push(t);

                names = list.filter(x => x.elementEnName == 'productName' && x.displayFlag1 != '0');
                t = this.getCombinedList(names, columns[1], '1');
                ret.push(t);
            }

            return ret;
        },
        isProductExist(productId) {
            let ret = false;
            let products = this.origin;
            if (products && products.length) {
                let t = products.filter(item => item.elementValue == productId);
                if (t && t.length) {
                    ret = true;
                }
            }

            return ret;
        },
        getBeforeProduct(list) {
            let ret = [];

            if (list && list.length) {
                let t = list.filter(x => x.displayFlag1 != '1');
                if (t && t.length) {
                    ret = this.parent.objectHelper.objectDeepCopy(t);
                }
            }

            return ret;
        },
        addProductCall(event) {
            let products = this.parent.objectHelper.objectDeepCopy(this.origin);
            let errorMsg = '';
            let columns = this.columns();

            if (this.parent.isRecover) {
                products = this.getBeforeProduct(products);
            }

            if (event && event.length) {
                for (let i = 0, len = event.length; i < len; i++) {

                    let element = event[i];
                    if (this.isProductExist(element.productId)) {
                        errorMsg = '不可以添加已经存在的产品';
                        break;
                    }

                    let num = products.length / 2 + 1;
                    if (num > 3) {
                        errorMsg = '产品数量不可以大于3';
                        break;
                    }

                    let sourceId = this.parent.objectHelper.objectDeepCopy(products[0]);
                    sourceId.displayFlag1 = '1';
                    sourceId[columns[3]] = element.productId;
                    sourceId.elementEnName = "productId"
                    sourceId.elementName = "产品编号"
                    sourceId.elementNum = "5"
                    sourceId.elementType = "4"
                    sourceId[columns[1]] = element.productId;
                    sourceId.rowNum = num;
                    if (this.parent.isRecover) {
                        sourceId[columns[0]] = '';
                        sourceId[columns[2]] = '';
                    }
                    products.push(sourceId);

                    let sourceName = this.parent.objectHelper.objectDeepCopy(products[0]);
                    sourceName.displayFlag1 = '1';
                    sourceName[columns[3]] = element.productName;
                    sourceName.elementEnName = "productName"
                    sourceName.elementName = "产品名称"
                    sourceName.elementNum = "10"
                    sourceName.elementType = "3"
                    sourceName[columns[1]] = element.productName;
                    sourceName.rowNum = num;
                    if (this.parent.isRecover) {
                        sourceName[columns[0]] = '';
                        sourceName[columns[2]] = '';
                    }
                    products.push(sourceName);
                }
            }
            if (errorMsg) {
                this.parent.commonlyMessage.logicError(errorMsg);
            } else {
                let p = this.parent.objectHelper.objectDeepCopy(products);
                let result = this.fixProduct(p);

                let t = result.filter(item => item.elementEnName == 'productId' && item.displayFlag1 == '1')[0];
                let allIds = t[columns[1]];
                this.parent.domAdjust.dispatcher({
                    key: 'productId',
                    val: allIds,
                    type: '2'
                })
                t = result.filter(item => item.elementEnName == 'productName' && item.displayFlag1 == '1')[0];
                let allNames = t[columns[1]];
                this.parent.domAdjust.dispatcher({
                    key: 'productName',
                    val: allNames,
                    type: '2'
                })

                this.parent.domAdjust.productFlag = false;
                this.changedProduct = this.parent.objectHelper.objectDeepCopy(products);
            }
        }
    }

    // DOM Factory
    generatorDom(list: any, type: number) {
        if (list && list.length) {
            list.forEach(element => {
                switch (element.elementType) {
                    case '1':
                        this.createDropdown(element, type);
                        break;
                    case '2':
                        this.createNumber(element, type);
                        break;
                    case '3':
                        this.createInput(element, type);
                        break;
                    case '4':
                        this.createProduct(element, type);
                        break;
                    case '5':
                        break;
                    case '6':
                        break;
                    case '7':
                        break;
                    case '8':
                        this.createCalender(element, type);
                        break;
                    case '9':
                        break;
                }
            })
        }
    }


    // 生成 Dom
    createDom(element: any, type: number, callback: Function) {
        let val: any;
        let title: string = element.elementName;
        let key: string = element.elementEnName;
        let container: ViewContainerRef = null;
        let flag: string = element.displayFlag1;

        let initCommand: string = element.initCommand;//by huj++
        // id, by huj++
        const id = element.id;
       
        if (type == 1) {
            val = element.elementValue;
            container = this.beforeContainer;
        } else {
            val = null;
            if (this.adjustElement == '013') {
                val = element.elementValue;
            }
            if (this.adjustElement == '014') {
                val = element.elementValue;
            }
            container = this.afterContainer;
        }

        callback({
            val: val,
            title: title,
            type: type,
            key: key,
            isNeedCallServe: element.frontElementCheckServe ? true : false,
            flag: flag,
            initCommand: initCommand,//by huj++
            id: id //by huj++
            // callType: element.frontElementCheckServe ? element.frontElementCheckServe.split(',')[0] : '',
        }, container);
    }

    // 创建 文本框
    createInput(element: any, type: number) {
        this.createDom(element, type, (param, container) => {
            this.domAdjust.createInput(param, container);
        });
    }
    // 创建 数字框
    createNumber(element: any, type: number) {
      //  this._decorateForNumber(element, type, () => {
            this.createDom(element, type, (param, container) => {
                this.domAdjust.createNumber(param, container);
            });
      //  })
    }
    _decorateForNumber(element: any, type: number, callback: Function) {
        if (this.adjustElement == '014' && type == 2) {
            if (element.elementEnName == 'payPeriod') {
                let temp = this.domAdjust.before.filter(x => x.elementEnName == 'payPeriod');
                if (temp && temp.length) {
                    element.elementValue = temp[0].elementValue;
                }
            }
            if (element.elementEnName == 'middleAccountNumber') {
                let temp = this.domAdjust.before.filter(x => x.elementEnName == 'middleAccountNumber');
                if (temp && temp.length) {
                    element.elementValue = temp[0].elementValue;
                }
            }
        }
        callback(element, type);
    }

    // 创建 日历
    createCalender(element: any, type: number) {
        this.createDom(element, type, (param, container) => {
            this.domAdjust.createCalender(param, container);
        });
    }
    // 创建 下拉框
    createDropdown(element: any, type: number) {
       //this._decorateForDropdown(element, type, () => {   //by huj--
            this.createDom(element, type, (param, container) => {
                let options: any = [];
                if (type == 1) {
                    options = [{ label: element.elementDesc, value: element.elementValue }];
                } else {
                    options = this.fixOptions(element.listElement);
                }
                //by huj start
                // if (this.adjustElement == '014' && type == 2
                // ) {
                //     this.domAdjust.after[3].frontElementCheckServe = '4,Disabled:true,middleAccountNumber';
                //     if (element.elementValue == '0' && element.elementEnName == 'havemiddleAccount') {
                //         this.domAdjust.after[4].initCommand = 'Disabled:true';
                //     }
                // }
                // by  huj end 

                param['options'] = options;
                this.domAdjust.createDropdown(param, container);
            });
        //});
    }

    _decorateForDropdown(element: any, type: number, callback: Function) {
        if (this.adjustElement == '014' && type == 2) {
            if (element.elementEnName == 'havemiddleAccount') {
                debugger;
                let temp = this.domAdjust.before.filter(x => x.elementEnName == 'havemiddleAccount');
                if (temp && temp.length) {
                    element.listElement.push({ optionKey: temp[0].elementValue, optionValue: temp[0].elementDesc });
                    element.elementValue = temp[0].elementValue;
                    element.elementDesc = temp[0].elementDesc;

                }
            }
        }
        callback(element, type);
    }


    // 创建 产品
    createProduct(element: any, type: number) {
        this.createDom(element, type, (param, container) => {
            this.domAdjust.createProduct(param, container);
        });
    }

    // 对下拉框数据进行处理
    fixOptions(list) {
        let ret = [];

        if (list && list) {
            ret.push({ label: '请选择', value: '' })
            list.forEach(element => {
                let obj = {};
                obj['label'] = element.optionValue;
                obj['value'] = element.optionKey;
                ret.push(obj);
            })
        }

        return ret;
    }


    // 要素调整事件
    change() {
        this.clearContainer();
        this.loadConfig();
    }

    // 清空 DOM 容器
    clearContainer() {
        this.beforeContainer.clear();
        this.afterContainer.clear();
    }

    specialDataOperateForSupply() {

        // 供应链模式变更
        // ----------------------------------特殊处理 start-------------------------
        let platformIdentify = this.domAdjust.before.filter(x => x.elementEnName == 'havemiddleAccount');
        platformIdentify = platformIdentify && platformIdentify.length ? platformIdentify[0].elementValue : '';
        let payPeriod = this.domAdjust.before.filter(x => x.elementEnName == 'payPeriod');
        payPeriod = payPeriod && payPeriod.length ? payPeriod[0].elementValue : 0;
        let havemiddleAccount = this.domAdjust.before.filter(x => x.elementEnName == 'havemiddleAccount');
        havemiddleAccount = havemiddleAccount && havemiddleAccount.length ? havemiddleAccount[0].elementValue : '';
        let middleAccountNumber = this.domAdjust.before.filter(x => x.elementEnName == 'middleAccountNumber');
        middleAccountNumber = middleAccountNumber && middleAccountNumber.length ? middleAccountNumber[0].elementValue : '';
        this.domAdjust.decorateForAfterDataMap(this.adjustElement, {
            platformIdentify: platformIdentify,
            payPeriod: payPeriod,
            havemiddleAccount: havemiddleAccount,
            middleAccountNumber: middleAccountNumber
        })
        // ----------------------------------特殊处理 end-------------------------
    }

    // 选人
    SelectPersonCover() {
        debugger;
        this.domAdjust.getBeforeAndAfter(this.adjustElement);
        let N = this.domAdjust.afterAdjust;
        if (typeof N != 'number' && !N) {
            this.commonlyMessage.logicError('请填写调整数据');
            return
        } else if (!this.adjustReason) {
            this.commonlyMessage.logicError('请填写调整原因');
            return
        } else if (!this.adjustOpinion) {
            this.commonlyMessage.logicError('请填写调整意见');
            return
        }

        if (this.isRecover) {
            this.selectPerson.loadData({
                userId: this.userId,
                taskId: this.taskId,
                adjustElement: this.adjustElement,
                beforeAdjust1: this.domAdjust.beforeAdjust,
                afterAdjust1: this.domAdjust.afterAdjust,
            })
            this.afterXX = this.domAdjust.afterAdjust
        } else {
            this.selectPerson.loadData({
                userId: this.userId,
                //taskId: this.taskId,
                adjustElement: this.adjustElement,
                beforeAdjust1: this.domAdjust.beforeAdjust,
                afterAdjust1: this.domAdjust.afterAdjust.toString(),
            })
            this.afterXX = this.domAdjust.afterAdjust.toString()
        }
        this.selectPerson.selectNextPerson();
    }

    // 提交
    submitFunc(callback: Function) {
        debugger;
        let params: any;
        if (this.adjustElement == '013') {
            params = this.domAdjust.getPostProducts(this.productFixer.changedProduct);
        } else {
            params = this.domAdjust.getPostData();
        }

        params['projectId'] = this.projectId;
        params['unitFlag'] = 2;
        params['adjustElement'] = this.adjustElement;
        params['adjustReason'] = this.adjustReason;
        params['adjustOpinion'] = this.adjustOpinion;
        if (this.isRecover) {
            params['adjustSeqno'] = this.adjustSeqno;
        }
         console.log(params)
         this.http.coopProjectElementAdjust(params).subscribe(data => {
             if (data['returnCode']['code'] == 'success') {
                 this.commonlyMessage.logicSuccess(data.returnCode.message);
                 callback();
                 this.goback();
             } else {
                 this.commonlyMessage.logicError(data.returnCode.message);
             }
             // 删除之前选中的人
             let s = JSON.parse(this.commfunc.getSessionData('commonHeader'));
             if (s.extParam.specifyNextOpers || s.extParam.specifyNextNode) {
                 delete s.extParam.specifyNextOpers;
                 delete s.extParam.specifyNextNode;
                 let commonHeader = s;
                 this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
             }
         })
    }

    // 获取新流水号
    getNewSerialNumer(callback: Function) {
        let param = {};
        this.customCenterHttpService.createGlobalSeqNo(param).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.setSerialNumber(data.globalSeqNo);
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
            }
            callback();
        }, error => {
            callback();
        })
    }

    // 设置流水号
    setSerialNumber(serialNum) {
        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader['globalSeqNo'] = serialNum;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));
    }

    // submit Factory
    submit(callback: Function) {

        if (this.isRecover) {
            this.setSerialNumber(this.mainFlowNo);
            this.submitFunc(callback);
        } else {
            this.getNewSerialNumer(() => {
                this.submitFunc(callback);
            })
        }

    }


    // 弹出框回调
    outValueCall(e) {
        let persons = '';
        let specifyNextNode = '';
        e.forEach(el => {
            persons += el['tellerId'] + ',';
            specifyNextNode = el.outFlowId;
        });

        let commonHeader = JSON.parse(this.commfunc.getSessionData('commonHeader'));
        commonHeader.extParam['specifyNextOpers'] = persons;
        commonHeader.extParam['specifyNextNode'] = specifyNextNode;
        this.commfunc.setSessionData('commonHeader', JSON.stringify(commonHeader));

        this.submit(() => {
            if (this.isRecover) {
                let params;
                if (this.adjustElement == '004') {
                    params = {
                        userId: this.userId,
                        taskId: this.taskId,
                        bussinessMap: {
                            specifyNextOpers: persons,
                            specifyNextNode: specifyNextNode,
                            userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
                            adjustElement: this.adjustElement,
                            beforeAdjust: this.domAdjust.beforeAdjust,
                            afterAdjust: this.afterXX,
                            beforeGuarantyStyle: this.domAdjust.beforeGuarantyStyle,
                            afterGuarantyStyle: this.domAdjust.afterGuarantyStyle
                        }
                    }
                } else {
                    params = {
                        userId: this.userId,
                        taskId: this.taskId,
                        bussinessMap: {
                            specifyNextOpers: persons,
                            specifyNextNode: specifyNextNode,
                            userId: JSON.parse(this.commfunc.getSessionData('commonHeader')).userId,
                            adjustElement: this.adjustElement,
                            beforeAdjust: this.domAdjust.beforeAdjust,
                            afterAdjust: this.afterXX,
                        }
                    }
                }
                this.pushToNext(params)
            }
        }
        );
    }

    // 提交到下一步
    pushToNext(params) {
        this.customService.bpmNextTask(params).subscribe(data => {
            if (data.returnCode.code == 'success') {
                this.commonlyMessage.logicSuccess(data.returnCode.message);
            } else {
                this.commonlyMessage.logicError(data.returnCode.message);
            }
        }, err => {
            this.commonlyMessage.logicError("调用服务失败！");
        })
    }


    //码值
    codeValues() {
        this.commfunc.codeValue();
        let ret = this.commfunc.codeDatas;
        return this.code = ret;
    }

    // 页面销毁
    ngOnDestory() {
        this.domAdjust.destroy();
    }

    // 页面返回
    goback() {
        // 跳转大额与供应链区分
        if (this.lei == '110') {
            this.router.navigate(['/pages/tzb/custom-center/project-main-manage/ProjectProtocolQuery'], { queryParams: { index: 1 } })
        } else if (this.lei == '999') {
            this.router.navigate(['/pages/tzb/custom-center/project-main-manage/project-main-manage-add-qita'], { queryParams: { index: 2 } })
        } else {
            setTimeout(() => {
                window.history.go(-1);
            }, 500)
        }

    }
}