import { Message } from "primeng/primeng";

export class CommonlyMessage {

    msgs: Message[] = [];

    constructor () {}

    logicError (message: string) {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '提示', detail: message });
    }

    logicSuccess (message: string) {
        this.msgs = [];
        this.msgs.push({ severity: 'success', summary: '提示', detail: message });
    }

    combineMsgs (msgs: any) {
        let ret: string = '';
        if (msgs && msgs.length) {
            let len = msgs.length;
            if (len > 1) {
                for (let i = 0; i < len - 1; i++) {
                    ret += msgs[i] + '<br>';
                }
                ret += msgs[len - 1];
            } else {
                ret = msgs[0];
            }
        }
        return ret;
    }

}